html {
  height: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
  min-height: 100%;
  min-width: 320px;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
}
.button {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  text-transform: uppercase;
  display: block;
  box-sizing: border-box;
  background-color: #68B738;
  display: flex;
  justify-content: center;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button--empty {
  width: 277px;
  display: block;
  box-sizing: border-box;
  padding: 9px 93px;
  font-size: 16px;
  line-height: 20px;
  background-color: #F2F2F2;
  color: #444444;
  text-decoration: none;
}
.button--empty:hover {
  color: #444444;
  background-color: #F2F2F2;
}
.button--empty:active {
  background-color: #F2F2F2;
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .button--empty {
    width: 247px;
    padding: 9px 76px;
    margin-bottom: 2px;
  }
}
@media (min-width: 1440px) {
  .button--empty {
    width: 170px;
    margin-left: 0;
    padding: 10px 40px;
  }
}
@media (min-width: 1440px) {
  .button--empty {
    width: 170px;
    margin-left: 0;
    padding: 10px 40px;
  }
}
