.main-nav {
  background-color: #ffffff;
  width: -1px;
  position: relative;
  margin-left: auto;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .main-nav {
    width: 497px;
    margin: 0;
    margin-bottom: 24px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: 650px;
    margin-left: 448px;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--open .main-nav__list {
    position: absolute;
    z-index: 3;
    margin: 0 auto;
    left: -339px;
    top: 16px;
    min-width: 360px;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list {
    position: static;
    display: block;
  }
}
@media (max-width: 767px) {
  .page-header .main-nav--nojs {
    margin-left: 0;
    width: 320px;
  }
}
.main-nav--closed .main-nav__toggle {
  width: 25px;
  height: 25px;
  position: absolute;
  top: -27px;
  right: 18px;
  z-index: 2;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  background-color: #000000;
  height: 1.5px;
  top: 0;
  left: 0;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle::before {
    display: none;
  }
}
.main-nav--open .main-nav__toggle {
  width: 25px;
  height: 25px;
  position: absolute;
  top: -19px;
  right: 28px;
  z-index: 2;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
.main-nav--open .main-nav__toggle::before,
.main-nav--open .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 9px;
  width: 24px;
  height: 1.5px;
  background-color: #111111;
}
.main-nav--open .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--open .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav--open .main-nav__toggle {
    display: none;
  }
}
.main-nav--nojs .main-nav__toggle,
.main-nav--nojs + .main-nav__toggle::before {
  display: none;
}
